@use "base/variables" as v;
@use "base/mixins" as m;

.esthetics-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$primaryBlueDark;
    font-size: 3rem;
    top: -2rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.esthetics-description {
  .esthetics-description-grid {
    display: grid;
    gap: 1rem;
    @include m.laptop {
      grid-template-columns: 1fr 3fr;
      gap: 3rem;
    }
    .esthetics-description-logo {
      display: flex;
      align-items: center;
      width: 100%;
      img {
        width: 100%;
      }
    }
    .esthetics-description-text {
      display: flex;
      align-items: center;
      font-family: v.$text;
      padding: 2rem;
      p {
        text-align: justify;
        font-size: 2rem;
      }
    }
  }
  padding: 5rem 2rem;
  @include m.mobile {
    padding: 7rem 5rem;
  }
  @include m.tablet {
    padding: 7rem 7rem;
  }
  @include m.desktop {
    padding: 12rem 15rem;
  }
  @include m.desktopXL {
    padding: 20rem 15rem;
  }
  background-color: v.$white;
  p {
    color: v.$secondaryGray;
    margin: 0;
    font-size: 1.5rem;
    line-height: 3rem;
    @include m.desktop {
      font-size: 2rem;
      line-height: 5rem;
    }
    @include m.desktopXL {
      font-size: 2.5rem;
      line-height: 5rem;
    }
    text-align: center;
    font-family: v.$text;
  }
}

.esthetics-elements {
  .esthetics-element-odd {
    margin-bottom: 45rem;
    .esthetics-element-title {
      font-size: 3rem;
      color: v.$white;
      background-color: v.$primaryBlue;
      padding: 2.5rem 5rem 2.5rem 0;
      width: 35%;
      text-align: end;
      border-radius: 0 5rem 5rem 0;
      position: relative;
    }
    .esthetics-element-description {
      background-color: v.$grayBackground;
      margin-left: 4rem;
      position: absolute;
      left: 15rem;
      top: 5rem;
      z-index: -1;
      width: 40rem;
      height: 40rem;
      text-align: center;
      color: v.$secondaryGray;
      font-size: 2rem;
      line-height: 2;
      display: flex;
      flex-direction: column;
      justify-content: center;
      // padding-top: 3rem;
      ul {
        list-style-position: inside;
      }
    }
    .esthetics-element-image {
      position: absolute;
      right: -25rem;
      bottom: -5rem;
      width: 30rem;
      img {
        width: 100%;
      }
    }
  }
  .esthetics-element-pair {
    margin-bottom: 45rem;
    .esthetics-element-title {
      font-size: 3rem;
      color: v.$white;
      background-color: v.$primaryBlue;
      padding: 2.5rem 0 2.5rem 5rem;
      width: 35%;
      text-align: start;
      border-radius: 5rem 0 0 5rem;
      margin-right: 0;
      margin-left: auto;
      position: relative;
    }
    .esthetics-element-description {
      background-color: v.$grayBackground;
      margin-left: 4rem;
      position: absolute;
      right: 15rem;
      top: 5rem;
      z-index: -1;
      width: 40rem;
      height: 40rem;
      text-align: center;
      color: v.$secondaryGray;
      font-size: 2rem;
      line-height: 2;
      display: flex;
      flex-direction: column;
      justify-content: center;
      // padding-top: 3rem;
      ul {
        list-style-position: inside;
      }
    }
    .esthetics-element-image {
      position: absolute;
      left: -25rem;
      bottom: -5rem;
      width: 30rem;
      img {
        width: 100%;
      }
    }
  }
  margin-bottom: 100rem;
}