@use "base/variables" as v;
@use "base/mixins" as m;

.services-procedures {
  background-color: v.$grayBackground;
  margin: 3rem;
  padding: 3rem;
  p,
  li {
    font-size: 1.5rem;
    font-family: v.$title;
  }
  ul {
    li {
      color: v.$primaryBlue;
    }
    p {
      color: v.$secondaryGray;
      margin-left: -2rem;
      margin-right: -1rem;
    }
  }
  ol {
    li {
      color: v.$secondaryGray;
    }
  }
}
