@use "base/variables" as v;
@use "base/mixins" as m;

.directory-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$primaryBlueDark;
    font-size: 3rem;
    top: -2rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.directory-grid {
  margin: 10rem 1rem;
  display: grid;
  grid-template-columns: 1fr;
  gap: 3rem;
  @include m.mobile {
    margin: 7rem 2rem;
  }
  @include m.tablet {
    margin: 5rem 2rem;
    grid-template-columns: 1fr 1fr;
  }
  @include m.desktop {
    grid-template-columns: 1fr;
    margin: 10rem auto;
    gap: 5rem;
    width: 90%;
  }
  .directory-text {
    .directory-text-align {
      display: flex;
      align-items: center;
    }
    display: grid;
    padding: 2rem 1.5rem;
    @include m.tablet {
      grid-template-rows: 3fr 1fr;
      padding: 2rem 2rem;
    }
    @include m.laptop {
      grid-template-rows: 3fr 1fr;
      padding: 2rem 2.5rem;
    }
    @include m.desktop {
      padding: 2rem 7.5rem;
    }
    p {
      vertical-align: middle;
      font-size: 1.25rem;
      font-family: v.$text;
      color: v.$secondaryGray;
      text-align: center;
      @include m.laptop {
        font-size: 1.75rem;
      }
    }
    .pink-btn {
      a {
        background-color: v.$pinkBtn;
        color: v.$pinkText;
        transition: all 0.5s;
        &:hover {
          background-color: darken($color: v.$pinkBtn, $amount: 5);
          transition: all 0.5s;
        }
      }
    }
    .directory-btn {
      display: flex;
      justify-content: center;
      align-items: flex-end;
    }
    a {
      font-size: 1.25rem;
      font-family: v.$text;
      color: v.$white;
      text-decoration: none;
      background-color: v.$primaryBlue;
      padding: 1rem 1.5rem;
      border-radius: 1.75rem;
      height: fit-content;
      @include m.mobile {
        padding: 1rem 3.5rem;
      }
      transition: all 0.5s;
      &:hover {
        background-color: darken($color: v.$blueBtn, $amount: 10);
        transition: all 0.5s;
      }
    }
  }
  .directory-logo {
    background-color: v.$primaryBlueDark;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: v.$white;
    font-family: v.$text;
    text-align: center;
    font-size: 1.25rem;
    padding: 1rem 0;
    @include m.tablet {
      font-size: 1.7rem;
      padding: 1.5rem 0;
    }
    @include m.desktop {
      font-size: 2rem;
      padding: 1.5rem 0;
    }
    @include m.desktopXL {
      font-size: 3rem;
      padding: 1.5rem 0;
    }
    .img-container {
      height: 6rem;
      margin-top: 1.5rem;
      margin-bottom: 1.5rem;
      @include m.laptop {
        height: 10rem;
        margin-top: 3rem;
        margin-bottom: 2.5rem;
      }
      img {
        height: 100%;
      }
    }
  }
  .directory-text {
    background-color: v.$grayBackground;
  }
  .directory-odd {
    display: grid;
    grid-template-rows: 1fr 2fr;
    @include m.desktop {
      grid-template-columns: 1fr 4fr;
      grid-template-rows: 1fr;
      gap: 3rem;
    }
    .directory-logo {
      border-radius: 5rem 5rem 0 0;
      @include m.desktop {
        border-radius: 10rem 0 0 0;
      }
    }
    .directory-text {
      border-radius: 0 0 0 0;
      @include m.desktop {
        border-radius: 0 0 10rem 0;
      }
    }
  }
  .directory-even {
    display: grid;
    grid-template-rows: 1fr 2fr;
    @include m.desktop {
      grid-template-columns: 4fr 1fr;
      grid-template-rows: 1fr;
      gap: 3rem;
    }
    .directory-logo {
      order: -1;
      border-radius: 5rem 5rem 0 0;
      @include m.desktop {
        order: 0;
        border-radius: 0 10rem 0 0;
      }
    }
    .directory-text {
      border-radius: 0 0 0 0;
      @include m.desktop {
        border-radius: 0 0 0 10rem;
      }
    }
  }
}

.service-head {
  position: relative;
  max-width: 120rem;
  margin: auto;
  margin-bottom: 70rem;
  h2 {
    text-transform: uppercase;
    position: absolute;
    top: 10rem;
    font-size: 4rem;
    .head-1 {
        color: v.$primaryBlueDark;
        text-transform: uppercase;
    }
  }
  .bottom-area {
    z-index: -1;
    width: 85%;
    background-color: v.$primaryBlue;
    position: absolute;
    top: 15rem;
    border-radius: 0 0 0 10rem;
    color: v.$white;
    padding: 3rem;
    font-size: 2rem;
  }
  .service-head-logo {
    position: absolute;
    right: 5rem;
    top: 2rem;
    padding: 2rem;
    background-color: v.$grayBackground;
    width: 20rem;
    height: 20rem;
    border-radius: 50% 50% 0 0;
    img {
      width: 100%;
      padding: 2rem 3rem 4rem 3rem;
    }
  }
}

.service-gallery {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 2rem;
    max-width: 120rem;
    margin: 0 auto 10rem auto;
    .service-img {
        width: 100%;
        border: 1rem solid v.$grayBackground;
        img {
            width: 100%;
        }
    }
}