@use "base/variables" as v;
@use "base/mixins" as m;

.main-services {
  display: grid;
  grid-template-columns: 1fr;
  margin: 1rem;
  @include m.mobile {
    margin: 6rem 6rem 2rem 6rem;
  }
  @include m.laptop {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    margin: 2rem;
    margin-bottom: 10rem;
  }
  @include m.desktop {
    gap: 4rem;
    margin: 5rem;
    margin-bottom: 10rem;
  }
  @include m.desktopXL {
    gap: 5rem;
    margin: 8rem;
  }
  .service {
    width: 100%;
    height: auto;
    border-radius: 10rem 10rem 0 0;
    @include m.laptop {
      border-radius: 30rem 30rem 0 0;
      height: 47rem;
    }
    @include m.desktop {
      height: 60rem;
    }
    @include m.desktopXL {
      height: 60rem;
    }
    background-color: v.$serviceBackground;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    margin-bottom: 5rem;
    @include m.laptop {
      margin-bottom: auto;
    }
    position: relative;
    .img-container {
      img {
        height: 100%;
      }
    }
    .img-container:not(.container-beauty) {
      margin-top: 3rem;
      margin-bottom: 0;
      height: 5rem;
      @include m.mobile {
        height: 6rem;
      }
      @include m.laptop {
        margin-top: 10rem;
        margin-bottom: 5rem;
      }
      @include m.desktop {
        height: 8rem;
      }
    }
    .img-container.container-beauty {
      margin-top: 5rem;
      margin-bottom: 0;
      height: 5rem;
      @include m.mobile {
        height: 5rem;
        margin-top: 4rem;
      }
      @include m.laptop {
        margin-top: 10rem;
        margin-bottom: 5rem;
      }
      @include m.desktop {
        height: 8rem;
      }
    }
    .service-text {
      font-family: v.$text;
      margin: 2.5rem 2.5rem 4rem 2.5rem;
      // margin: 4rem 4rem 8rem 4rem;
      text-align: center;
      font-size: 1.5rem;
      color: v.$secondaryGray;
      @include m.laptop {
        font-size: 1.5rem;
        margin: 1.5rem 4rem;
      }
      @include m.desktop {
        font-size: 2rem;
        margin: 2rem 6rem;
      }
      @include m.desktopXL {
        font-size: 2rem;
        margin: 2rem 8rem;
      }
    }
    button {
      border: none;
      font-family: v.$title;
      // font-size: 1.5rem;
      background-color: transparent;
      color: v.$white;
      text-align: center;
    }
    .service-btn {
      position: absolute;
      bottom: -2.25rem;
      font-family: v.$title;
      font-size: 1.5rem;
      background-color: v.$blueBtn;
      color: v.$white;
      text-align: center;
      border-radius: 0 0 3rem 3rem;
      margin: auto 5rem;
      width: 100%;
      padding: 1.5rem 0;
      @include m.laptop {
        width: 80%;
        // padding: 1rem 5rem;
        font-size: 1.75rem;
        border-radius: 3rem;
      }
      @include m.desktop {
        // padding: 1rem 8rem;
        font-size: 2.5rem;
        border-radius: 3rem;
      }
      transition: all 0.5s;
      &:hover {
        background-color: darken($color: v.$blueBtn, $amount: 10);
        transition: all 0.5s;
      }
    }
    .service-btn-odd {
      background-color: v.$pinkBtn;
      color: v.$pinkText;
      transition: all 0.5s;
      &:hover {
        background-color: darken($color: v.$pinkBtn, $amount: 5);
        transition: all 0.5s;
      }
    }
  }
}
