@use "base/variables" as v;
@use "base/mixins" as m;

.lab-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$white;
    font-size: 3rem;
    top: -2rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.lab-description {
  .lab-description-grid {
    display: grid;
    gap: 1rem;
    @include m.laptop {
      grid-template-columns: 1fr 3fr;
      gap: 3rem;
    }
    .lab-description-logo {
      display: flex;
      align-items: center;
      width: 100%;
      img {
        width: 100%;
      }
    }
    .lab-description-text {
      display: flex;
      align-items: center;
      font-family: v.$text;
      padding: 2rem;
      p {
        text-align: justify;
        font-size: 2rem;
      }
    }
  }
  padding: 5rem 2rem;
  @include m.mobile {
    padding: 7rem 5rem;
  }
  @include m.tablet {
    padding: 7rem 7rem;
  }
  @include m.desktop {
    padding: 12rem 15rem;
  }
  @include m.desktopXL {
    padding: 20rem 15rem;
  }
  background-color: v.$primaryBlueDark;
  p {
    color: v.$white;
    margin: 0;
    font-size: 1.5rem;
    line-height: 3rem;
    @include m.desktop {
      font-size: 2rem;
      line-height: 5rem;
    }
    @include m.desktopXL {
      font-size: 2.5rem;
      line-height: 5rem;
    }
    text-align: center;
    font-family: v.$text;
  }
}

.lab-procedures {
  background-color: v.$grayBackground;
  margin: 3rem;
  padding: 3rem;
  p,
  li {
    font-size: 1.5rem;
    font-family: v.$title;
  }
  li {
    color: v.$primaryBlue;
  }
  ul {
    p {
      margin-left: -2rem;
      margin-right: -1rem;
    }
  }
}

.lab-procedures-list {
  margin: 5rem;
  height: 40rem;
  .lab-procedures-list-head {
    position: relative;
    h2 {
      margin-left: 5rem;
      text-transform: uppercase;
      position: absolute;
      top: 10rem;
      font-size: 4rem;
      .lab-head-2 {
        display: block;
        z-index: 3;
        color: v.$white;
      }
    }
    .bottom-area {
      z-index: -1;
      height: 17rem;
      width: 85%;
      background-color: v.$primaryBlue;
      position: absolute;
      top: 15rem;
      border-radius: 0 0 0 10rem;
    }
    .lab-head-logo {
      position: absolute;
      right: 5rem;
      top: 2rem;
      padding: 2rem;
      background-color: v.$grayBackground;
      width: 20rem;
      height: 20rem;
      border-radius: 50% 50% 0 0;
      img {
        width: 100%;
        padding: 2rem 3rem 4rem 3rem;
      }
    }
  }
}

// Pagination

.lab-pagination {
  max-width: 120rem;
  margin: auto;
  margin-bottom: 30rem;
  .panel {
    display: flex;
    justify-content: end;
    margin: 2rem;
    font-size: 1.25rem; 
  }
  .input-group {
    label {
      text-transform: uppercase;
      padding: 1rem 2rem 1rem 1rem;
    }
    input {
      border: 1px solid v.$primaryBlueDark;
      padding: 1rem;
      width: 20rem;
    }
  }
  .lab-column {
    padding-left: 2rem;
    border-left: 2px solid v.$primaryBlueDark;
  }
  tr {
    border-style: hidden;
    height: 4rem;
    vertical-align: middle;
  }
  td {
    font-size: 1.5rem;
  }
  .lab-price {
    border-style: hidden;
    margin-right: 2rem;
    display: flex;
    justify-content: space-between;
  }
  .paginate_controls {
    display: flex;
    justify-content: center;
    margin: 3rem;
    input[type=button]:not(.paginate_control_prev,.paginate_control_next) {
      border-radius: 50%;
      width: 3.5rem;
      height: 3.5rem;
      margin: 1rem 0.5rem 0.5rem 0.5rem;
      font-size: 1.5rem;
      border: 2px solid v.$primaryBlueDark;
      &:hover {
        background-color: v.$primaryBlueDark;
        color: v.$white;
      }
    }
    input[type=button]:not(.active){
      background-color: transparent;
    }
    .active {
      background-color: v.$primaryBlueDark;
      color: v.$white;
    }
    .paginate_control_prev,
    .paginate_control_next {
      background-color: transparent;
      border: none;
      font-size: 4rem;
      color: v.$primaryBlueDark
    }
  }
}