html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
}

h2 {
  font-size: 1.25rem;
}
@media (min-width: 320px) {
  h2 {
    font-size: 1.5rem;
  }
}
@media (min-width: 481px) {
  h2 {
    font-size: 1.75rem;
  }
}
@media (min-width: 1025px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.5rem;
}
@media (min-width: 320px) {
  h3 {
    font-size: 1.75rem;
  }
}
@media (min-width: 481px) {
  h3 {
    font-size: 1.9rem;
  }
}
@media (min-width: 1025px) {
  h3 {
    font-size: 2.2rem;
  }
}
@media (min-width: 1201px) {
  h3 {
    font-size: 2.5rem;
  }
}

p {
  font-size: 1.2rem;
}
@media (min-width: 320px) {
  p {
    font-size: 1.4rem;
  }
}
@media (min-width: 481px) {
  p {
    font-size: 1.5rem;
  }
}
@media (min-width: 1025px) {
  p {
    font-size: 1.7rem;
  }
}
@media (min-width: 1201px) {
  p {
    font-size: 1.9rem;
  }
}

form p {
  font-size: 1.1rem;
}
@media (min-width: 320px) {
  form p {
    font-size: 1.35rem;
  }
}
@media (min-width: 481px) {
  form p {
    font-size: 1.45rem;
  }
}
@media (min-width: 1025px) {
  form p {
    font-size: 1.65rem;
  }
}
@media (min-width: 1201px) {
  form p {
    font-size: 1.85rem;
  }
}

button,
.btn {
  font-size: 1.25rem;
}
@media (min-width: 320px) {
  button,
.btn {
    font-size: 1.5rem;
  }
}
.form-select,
.form-control,
.invalid-feedback {
  font-size: 1rem;
}
@media (min-width: 320px) {
  .form-select,
.form-control,
.invalid-feedback {
    font-size: 1.25rem;
  }
}
@media (min-width: 768px) {
  .form-select,
.form-control,
.invalid-feedback {
    font-size: 1.75rem;
  }
}
label {
  font-size: 1.5rem;
}
@media (min-width: 320px) {
  label {
    font-size: 1.75rem;
  }
}
@media (min-width: 768px) {
  label {
    font-size: 2rem;
  }
}
.header-top {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 5rem;
  background-color: #dfe7ec;
  font-family: "CamingoDosProCd-SemiBold";
}
@media (min-width: 320px) {
  .header-top {
    justify-content: space-between;
  }
}
.header-top p {
  color: #747474;
}

.header-phone {
  display: none;
  align-items: center;
}
@media (min-width: 320px) {
  .header-phone {
    display: flex;
  }
}
.header-phone > * {
  margin: 1rem;
}
.header-phone .img-container {
  height: 2rem;
  padding-left: 1rem;
}
@media (min-width: 768px) {
  .header-phone .img-container {
    padding-left: 3rem;
  }
}
.header-phone .img-container img {
  height: 100%;
}

.header-social-media {
  display: flex;
  align-items: center;
}
.header-social-media .img-container {
  height: 2rem;
  margin: 1.5rem;
}
@media (min-width: 768px) {
  .header-social-media .img-container {
    margin: 3rem;
  }
}
.header-social-media .img-container:last-child {
  padding-right: 1rem;
}
@media (min-width: 768px) {
  .header-social-media .img-container:last-child {
    padding-right: 2rem;
  }
}
.header-social-media .img-container img {
  height: 100%;
}
.header-middle {
  height: 15rem;
  background-color: #1a9bc4;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header-middle {
    height: 20rem;
  }
}
.header-middle .img-container {
  height: 7.5rem;
}
@media (min-width: 768px) {
  .header-middle .img-container {
    height: 12.5rem;
  }
}
.header-middle .img-container img {
  height: 100%;
}

.navbar {
  background-color: #dfe7ec;
  padding-bottom: 0;
  padding-top: 0;
}
.navbar .container-fluid {
  padding: 0;
}
.navbar .navbar-toggler {
  margin: 1rem auto;
  padding: 0.75rem;
}

@media (min-width: 768px) {
  .navbar-expand-md .navbar-collapse {
    display: block !important;
  }
}
.navbar-toggler:focus {
  box-shadow: none;
}

.header-bottom {
  height: auto;
  background-color: #dfe7ec;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .header-bottom {
    height: 7rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom {
    height: 10rem;
  }
}
.header-bottom .navbar {
  background-color: #dfe7ec;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
@media (min-width: 768px) {
  .header-bottom .navbar {
    flex-direction: row;
  }
}
.header-bottom .navbar .navbar-element {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
@media (min-width: 768px) {
  .header-bottom .navbar .navbar-element {
    width: 20%;
    height: 7rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .navbar-element {
    height: 10rem;
  }
}
.header-bottom .navbar .navbar-element .img-container {
  height: 2rem;
}
@media (min-width: 768px) {
  .header-bottom .navbar .navbar-element .img-container {
    height: 2rem;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .navbar-element .img-container {
    height: 3rem;
  }
}
.header-bottom .navbar .navbar-element .img-container img {
  height: 100%;
}
.header-bottom .navbar .navbar-element p {
  font-family: "CodeProBoldLC";
  margin: 1.35rem;
}
@media (min-width: 768px) {
  .header-bottom .navbar .navbar-element p {
    font-size: 1.2rem;
    margin: 1rem;
    margin-right: 0;
  }
}
@media (min-width: 1025px) {
  .header-bottom .navbar .navbar-element p {
    font-size: 1.6rem;
  }
}
@media (min-width: 1201px) {
  .header-bottom .navbar .navbar-element p {
    font-size: 1.75rem;
    margin: 2rem;
  }
}
.header-bottom .navbar a {
  text-decoration: none;
  color: #747474;
}
.header-bottom .navbar a:hover {
  background-color: #cfdbe3;
}

.main-hero .hero-text {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Gotham-Bold";
  color: #1a9bc4;
  font-size: 2rem;
  margin: auto;
}
.main-hero .hero-text h2 {
  margin: 6rem 2rem;
  text-align: center;
}
@media (min-width: 320px) {
  .main-hero .hero-text h2 {
    margin: 6rem;
  }
}
@media (min-width: 768px) {
  .main-hero .hero-text h2 {
    margin-top: 15rem;
    margin-bottom: 7rem;
    font-size: 2.5rem;
  }
}
@media (min-width: 1025px) {
  .main-hero .hero-text h2 {
    margin: 20rem 15rem 10rem 15rem;
    font-size: 2.5rem;
  }
}
@media (min-width: 1201px) {
  .main-hero .hero-text h2 {
    margin-top: 30rem;
    margin-bottom: 10rem;
    margin: 30rem 35rem 10rem 35rem;
    font-size: 3.5rem;
  }
}
.main-hero .img-container {
  margin-top: -2rem;
  margin-bottom: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 8rem;
}
@media (min-width: 768px) {
  .main-hero .img-container {
    height: 12rem;
    margin-top: 0rem;
    margin-bottom: 15rem;
  }
}
@media (min-width: 1025px) {
  .main-hero .img-container {
    height: 15rem;
    margin-bottom: 15rem;
  }
}
@media (min-width: 1201px) {
  .main-hero .img-container {
    margin-bottom: 25rem;
  }
}
.main-hero .img-container img {
  height: 100%;
}

.main-bg-image {
  position: absolute;
  top: 42rem;
  z-index: -1;
  width: 100%;
}
.main-bg-image .img-container {
  width: 100%;
}
.main-bg-image .img-container img {
  width: 100%;
}
@media (min-width: 768px) {
  .main-bg-image {
    top: 32rem;
  }
}
@media (min-width: 1025px) {
  .main-bg-image {
    top: 35rem;
  }
}

.main-services {
  display: grid;
  grid-template-columns: 1fr;
  margin: 1rem;
}
@media (min-width: 320px) {
  .main-services {
    margin: 6rem 6rem 2rem 6rem;
  }
}
@media (min-width: 768px) {
  .main-services {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    margin: 2rem;
    margin-bottom: 10rem;
  }
}
@media (min-width: 1025px) {
  .main-services {
    gap: 4rem;
    margin: 5rem;
    margin-bottom: 10rem;
  }
}
@media (min-width: 1201px) {
  .main-services {
    gap: 5rem;
    margin: 8rem;
  }
}
.main-services .service {
  width: 100%;
  height: auto;
  border-radius: 10rem 10rem 0 0;
  background-color: #deebef;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  margin-bottom: 5rem;
  position: relative;
}
@media (min-width: 768px) {
  .main-services .service {
    border-radius: 30rem 30rem 0 0;
    height: 47rem;
  }
}
@media (min-width: 1025px) {
  .main-services .service {
    height: 60rem;
  }
}
@media (min-width: 1201px) {
  .main-services .service {
    height: 60rem;
  }
}
@media (min-width: 768px) {
  .main-services .service {
    margin-bottom: auto;
  }
}
.main-services .service .img-container img {
  height: 100%;
}
.main-services .service .img-container:not(.container-beauty) {
  margin-top: 3rem;
  margin-bottom: 0;
  height: 5rem;
}
@media (min-width: 320px) {
  .main-services .service .img-container:not(.container-beauty) {
    height: 6rem;
  }
}
@media (min-width: 768px) {
  .main-services .service .img-container:not(.container-beauty) {
    margin-top: 10rem;
    margin-bottom: 5rem;
  }
}
@media (min-width: 1025px) {
  .main-services .service .img-container:not(.container-beauty) {
    height: 8rem;
  }
}
.main-services .service .img-container.container-beauty {
  margin-top: 5rem;
  margin-bottom: 0;
  height: 5rem;
}
@media (min-width: 320px) {
  .main-services .service .img-container.container-beauty {
    height: 5rem;
    margin-top: 4rem;
  }
}
@media (min-width: 768px) {
  .main-services .service .img-container.container-beauty {
    margin-top: 10rem;
    margin-bottom: 5rem;
  }
}
@media (min-width: 1025px) {
  .main-services .service .img-container.container-beauty {
    height: 8rem;
  }
}
.main-services .service .service-text {
  font-family: "BaseticaPro-Med";
  margin: 2.5rem 2.5rem 4rem 2.5rem;
  text-align: center;
  font-size: 1.5rem;
  color: #747474;
}
@media (min-width: 768px) {
  .main-services .service .service-text {
    font-size: 1.5rem;
    margin: 1.5rem 4rem;
  }
}
@media (min-width: 1025px) {
  .main-services .service .service-text {
    font-size: 2rem;
    margin: 2rem 6rem;
  }
}
@media (min-width: 1201px) {
  .main-services .service .service-text {
    font-size: 2rem;
    margin: 2rem 8rem;
  }
}
.main-services .service button {
  border: none;
  font-family: "Gotham-Bold";
  background-color: transparent;
  color: #ffffff;
  text-align: center;
}
.main-services .service .service-btn {
  position: absolute;
  bottom: -2.25rem;
  font-family: "Gotham-Bold";
  font-size: 1.5rem;
  background-color: #2ba7dd;
  color: #ffffff;
  text-align: center;
  border-radius: 0 0 3rem 3rem;
  margin: auto 5rem;
  width: 100%;
  padding: 1.5rem 0;
  transition: all 0.5s;
}
@media (min-width: 768px) {
  .main-services .service .service-btn {
    width: 80%;
    font-size: 1.75rem;
    border-radius: 3rem;
  }
}
@media (min-width: 1025px) {
  .main-services .service .service-btn {
    font-size: 2.5rem;
    border-radius: 3rem;
  }
}
.main-services .service .service-btn:hover {
  background-color: #1d89b8;
  transition: all 0.5s;
}
.main-services .service .service-btn-odd {
  background-color: #ffccd9;
  color: #e26d93;
  transition: all 0.5s;
}
.main-services .service .service-btn-odd:hover {
  background-color: #ffb3c6;
  transition: all 0.5s;
}

.main-description {
  display: grid;
  grid-template-columns: 1fr;
  margin: 4rem;
  margin-top: 0;
  gap: 5rem;
}
@media (min-width: 1025px) {
  .main-description {
    margin: 15rem;
  }
}
.main-description .description-image {
  width: 100%;
}
.main-description .description-image .img-container {
  width: 100%;
}
.main-description .description-image .img-container img {
  width: 100%;
}
.main-description .description-text h3 {
  color: #1a9bc4;
  font-family: "Gotham-Bold";
  text-align: center;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .main-description .description-text h3 {
    font-size: 2.5rem;
  }
}
@media (min-width: 1201px) {
  .main-description .description-text h3 {
    font-size: 3rem;
  }
}
.main-description .description-text p {
  font-family: "BaseticaPro-Med";
  color: #747474;
  text-align: center;
}
@media (min-width: 768px) {
  .main-description .description-text p {
    font-size: 1.6rem;
  }
}
@media (min-width: 1201px) {
  .main-description .description-text p {
    font-size: 1.75rem;
  }
}

.grid-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.main-grid {
  width: 75%;
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .main-grid {
    grid-template-columns: repeat(12, 1fr);
