@use "base/variables" as v;
@use "base/mixins" as m;

.fertility-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$white;
    font-size: 3rem;
    top: -2rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.fertility-description {
    height: 40rem;
    padding: 5rem 2rem;
    @include m.mobile {
        padding: 7rem 5rem;
    }
    @include m.tablet {
        padding: 7rem 7rem;
    }
    @include m.desktop {
        padding: 12rem 15rem;
    }
    @include m.desktopXL {
        padding: 20rem 15rem;
    }
  background-color: v.$primaryBlueDark;
  p {
    color: v.$white;
    margin: 0;
    font-size: 1.5rem;
    line-height: 3rem;
    @include m.desktop {
        font-size: 2rem;
        line-height: 5rem;
    }
    @include m.desktopXL {
        font-size: 2.5rem;
        line-height: 5rem;
    }
    text-align: center;
    font-family: v.$text;
  }
}

.fertility-fertihaus {
  background-color: v.$pinkText;
  display: grid;
  grid-template-columns: 1fr 2fr;
  max-width: 120rem;
  margin: auto;
  border-radius: 0 10rem 0 0;
  .fertihaus-logo {
    border-right: 2px solid v.$white;
    padding: 5rem 6rem;
    img {
      width: 100%;
    }
  }
  .fertihaus-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    p {
      color: v.$white;
      font-size: 2rem;
      padding: 5rem 5rem 2rem 5rem;
      text-align: center;
    }
    a {
      background-color: v.$pinkBtn;
      text-decoration: none;
      color: v.$pinkText;
      font-size: 3rem;
      padding: 1rem 2rem;
      border-radius: 3rem;
      margin-bottom: 5rem;
    }
  }
}